import { Injectable } from '@angular/core';
import { Employee,Login } from './employee.model';

import {HttpClient} from '@angular/common/http'
import { Observable, observable } from 'rxjs';
import { NgForOf } from '@angular/common';
import { HttpHeaders } from '@angular/common/http';

@Injectable({
  providedIn: 'root'
})
export class EmployeeService {
  readonly headersJson = new HttpHeaders().set('Content-Type', 'application/json');
  formdata : Employee;
  LoginData:Login;
  list : Employee[]; 
   readonly RootUrl ="http://183.82.112.191:10262/api/"
 // readonly RootUrl ="http://localhost:52051/api/"
  constructor(private http : HttpClient) { 


    
  }
 

  refreshList() 
  {
   
    // this.http.get(this.RootUrl+'WebApiEmp/EmpGet').toPromise().then(res=>this.list = res as  Employee[]);
    return this.http.get<Employee[]>(this.RootUrl+'AngularApi/EmpGet').toPromise().then(res=>this.list = res as  Employee[]);
  }
  PostEmployee(formdata:Employee)
  {
    console.log(formdata);
return this.http.post(this.RootUrl+'AngularApi/EmpCreate',formdata);
  }
  PutEmployee(formdata:Employee)
  {
return this.http.post(this.RootUrl+'AngularApi/EmpUpdate',formdata);
  }
  DeleteEmployee(id : number)
  {
    console.log(id);
    return this.http.delete(this.RootUrl+'AngularApi/EmpDelete/'+id);

  }

validate(LoginData:Login)
{
  return this.http.post(this.RootUrl+'AngularApi/Login',LoginData);
}

}
